const Container = props => {
  return (
    <div
      className={props.className ? `container ${props.className}` : "container"}
      id={props.id ? props.id : ""}
    >
      {props.children}
      <style>{`
      .container {
        width: 100%;
        padding: 1rem;
      }
  `}</style>
    </div>
  );
};

export default Container;
